.rootInner {
    width: 100vw;
    height: 100vh;
    background-color: #FFF;
}

/* ================================= LOADER ================================= */

.rootLoader {
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 10;
    width: 100vw;
    height: 100vh;
    background-color: #FFF;
    background-image: url(./bin/black-jacket-suiting-logo.png);
    background-position: center calc(50vh - 80px);
    background-repeat: no-repeat;
}

.rootLoaderSpin {
    width: 80px;
    height: 80px;
    background-image: url(./bin/tail-spin.svg);
    background-position: center;
    background-repeat: no-repeat;
    margin: auto;
    margin-top: calc(50vh + 60px);
    opacity: 0.7;
}

.rootLoaded {
    animation-duration: 0.15s;
    animation-name: loaded;
    animation-fill-mode: forwards;
    animation-iteration-count: initial;
}

@keyframes loaded {
    0% { opacity: 1; display: block; }
    99.9% { opacity: 0; display: block; width: 100vw; height: 100vh; }
    100% { opacity: 0; display: none; width: 0px; height: 0px; }
}

.rootLayout > div {
    position: fixed;
    top: 0px;
    height: 100vh;
}

.rootRibbon {
    width: 70px;
    left: 0px;
    background-color: #222;
    background-image: url(./bin/black-jacket-suiting-logo-white-vertical.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 70% auto;
}

.rootProducts {
    left: 70px;
    width: calc(100vw - 650px);
    height: 100vh;
    background-color: #fff;
    box-shadow: 0px 0px 30px #0003;
    border-right: 1px solid #1113;
}

.SearchBox {
    width: calc(100% - 70px);
    border: none;
    background-color: #3331;
    height: 49px;
    margin: 0px;
    padding: 0px 25px 0px 45px;
    font-size: 18px;
    font-family: 'Roboto Light';
    background-image: url("data:image/svg+xml,<svg id='svg' viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg' style='transform: rotate(0deg); fill: rgba(0, 0, 0, 0.45);'><path d='M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z'></path></svg>");
    background-position: 15px 15px;
    background-repeat: no-repeat;
    background-size: 20px auto;
    border-bottom: 1px solid #1112;
}

.ProductsTray {
    width: inherit;
    height: calc(100vh - 50px);
    overflow-y: auto;
}

.ProductsTrayLoadingFirst {
    background-image: url(./bin/tail-spin.svg);
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.7;
}

.ProductsTrayLoadingFirst::after {
    display: none !important;
}

.ProductsTray::after {
    content: '.';
    display: block;
    height: 130px;
    color: transparent;
}

.ProductsTrayLoading::after {
    content: '.';
    color: transparent;
    display: block;
    height: 130px;
    background-image: url(./bin/tail-spin.svg);
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.7;
}

.ProductsTrayEnd::after {
    content: 'End of Result';
    text-align: center;
    color: #1118;
    display: block;
    height: 100px;
    line-height: 100px;
}

.ProductsTrayEmpty::after {
    content: 'Sorry, No result found :(';
    text-align: center;
    color: #1118;
    display: block;
    height: 100px;
    line-height: 100px;
}

.ProductItem {
    width: 180px;
    height: 220px;
    display: inline-block;
    overflow: hidden;
    margin: 20px 0px 0px 20px;
    cursor: pointer;
    transition: all 0.2s;
    padding-top: 0px;
}

.ProductItemLoading {
    opacity: 0;
    margin: 20px 0px 0px 20px;
    padding-top: 20px;
    height: 200px;
}

.ProductItem:hover {
    box-shadow: 0px 0px 25px #1114;
}

.ProductItemThumb {
    width: inherit;
    height: 180px;
    background-size: 65% auto;
    background-position: center;
    background-repeat: no-repeat;
    transition: opacity 0.2s;
}

.ProductItemThumbLoading {
    opacity: 0;
}

.ProductItemThumb > img {
    width: 0px;
    height: 0px;
}

.ProductItemTitle {
    height: 40px;
    line-height: 20px;
    width: 140px;
    margin: 0px 20px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
    font-size: 12px;
}

.rootViewer {
    width: 570px;
    left: calc(100vw - 570px);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 70% auto;
}